.UserDialog-Wrapper {
    position: fixed; top: 0; right: 0; bottom: 0; left: 0;     /*全屏*/
    display: -ms-flexbox;
    display: flex; -ms-flex-pack: center; justify-content: center; -ms-flex-align: center; align-items: center;     /*内容居中*/
}
.UserDialog {
    min-width: 20em;
    background: white;
    border: 1px solid #aaa;
    border-radius: 4px;
    -webkit-box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
            box-shadow: 0 0 1em hsla(0,0%,0%,0.35);
}

.UserDialog > nav:first-child { margin-left: -2em; padding: 1em; }
.UserDialog > nav:first-child label { margin-left: 2em; cursor: pointer; }

.UserDialog > .panes { padding: 0 1em 1em; }

.UserDialog form > .row { display: -ms-flexbox; display: flex; padding: .3em 0; }
.UserDialog form > .row > label:first-child { width: 4em; }
.UserDialog form > .row.actions { padding-left: 4em; }



